<template>
    <div>
        <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
        >
            <div style="position: fixed;width: 26rem;z-index: 999999">
                <div>
                    <van-nav-bar
                            :title="vod_name"
                            left-text="返回"
                            left-arrow
                            @click-left="onClickLeft"
                    />
                </div>
                <div>
                    <video ref="video" controls style="width: 100%"></video>
                </div>
            </div>
            <div style="padding-top: 17rem;">
                <div style="margin-left: 0.8rem">
                    <h4>播放源：</h4>
                    <van-button v-if="tkm3u8" plain type="info" size="mini" @click="changeYuan(1)">天空m3u8</van-button>
                    <van-button v-if="tkyun" plain type="info" size="mini" @click="changeYuan(0)">天空在线</van-button>
                </div>
                <div style="margin-left: 0.8rem;margin-top: 0.5rem">
                    <div v-if="yuan==0" v-for="(item,index) in videoDetail.playUrl0">
                        <van-button plain type="info" size="mini" @click="getStrem(item.url)">{{item.name}}</van-button>
                    </div>
                    <div v-if="yuan==1" v-for="(item,index) in videoDetail.playUrl1">
                        <van-button  plain type="info" size="mini" @click="getStrem(item.url)">{{item.name}}</van-button>
                    </div>
                </div>
                <br>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">剧名：</span></van-col>
                    <van-col span="21">{{videoDetail.vodName}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">分类：</span></van-col>
                    <van-col span="21">{{videoDetail.vodClass}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">主演：</span></van-col>
                    <van-col span="21" v-if="videoDetail.actor">{{videoDetail.actor}}</van-col>
                    <van-col span="21" v-else>未知</van-col>
                </van-row>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">导演：</span></van-col>
                    <van-col span="21" v-if="videoDetail.vodDirector">{{videoDetail.vodDirector}}</van-col>
                    <van-col span="21" v-else>未知</van-col>
                </van-row>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">地区：</span></van-col>
                    <van-col span="21">{{videoDetail.vodArea}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">语言：</span></van-col>
                    <van-col span="21">{{videoDetail.vodLang}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">年份：</span></van-col>
                    <van-col span="21">{{videoDetail.vodYear}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="3"><span style="float: right;font-weight: bold">简介：</span></van-col>
                    <van-col span="21" v-html="videoDetail.vodContent"></van-col>
                </van-row>
            </div>
        </van-pull-refresh>
    </div>
</template>
<style>
body{
    font-size: 14px;
    font-family: -webkit-body;
}
</style>
<script>
    import $ from "jquery";
    import Hls from 'hls.js';
    export default {
        data() {
            return {
                vod_name:'',
                vod_id:'',
                isLoading:false,
                hls:'',
                videoDetail:{},
                tkm3u8:false,
                tkyun:false,
                yuan:1,
                url:'',
            }
        },
        created() {
            this.vod_name = this.$route.query.vod_name;
            this.vod_id=this.$route.query.vod_id;
            this.getData()
        },
        methods: {
            getData(){
                let _this=this;
                $.ajax({
                    url: _this.baseUrl + "/user/video",
                    data: {vod_id:_this.vod_id},
                    type: 'post',
                    traditional: true,
                    async: true,
                    dataType: "json",
                    success: function (data) {
                        _this.videoDetail=data.data;
                        if(_this.videoDetail.playFrom=='0,1'){
                            _this.tkyun=true;
                            _this.tkm3u8=true;
                            _this.getStrem(_this.videoDetail.playUrl1[0].url)
                        }else if(_this.videoDetail.playFrom=='0'){
                            _this.tkyun=true;
                            _this.getStrem(_this.videoDetail.playUrl0[0].url)
                        }else if(_this.videoDetail.playFrom=='1'){
                            _this.tkm3u8=true;
                            _this.getStrem(_this.videoDetail.playUrl1[0].url)
                        }
                        _this.isLoading=false;
                    }
                });
            },
            onClickLeft() {
                this.$router.go(-1)
            },
            onRefresh() {
                let _this=this;
                setTimeout(function () {
                    _this.getData()
                },2000)
            },
            getStrem(url){
                if (Hls.isSupported()) {
                    this.hls = new Hls();
                    this.url= url;
                    this.hls.loadSource(url);
                    this.hls.attachMedia(this.$refs.video);
                    this.hls.on(Hls.Events.MANIFEST_PARSED, () => {
                        console.log('加载成功');
                        this.$refs.video.play();
                    });
                    this.hls.on(Hls.Events.ERROR, (event, data) => {
                         console.log(event, data);
                        // 监听出错事件
                        console.log('加载失败');
                    });
                }
            },
            changeYuan(yuan){
                this.yuan=yuan;
            }
        },
    };
</script>
